<template>
  <div>
    <div class="study-card common-bor">
      <div class="study-card-outter">
        <ul class="study-card-list clear">
          <li
            v-for="(item, index) in imgArr"
            :class="{ media: item.file_url !== '1' }"
            @click="look(item.file_url, index, item.media_type)"
            :key="index"
          >
            <!--media_type 1.图片 2.音频 3.视频-->
            <div
              :style="{ height: item.thumbH + 'rem' }"
              v-if="item.media_type === '1'"
            >
              <div v-if="isNeedShowImgName" class="img-name">
                {{ item.name }}
              </div>
              <img
                :src="item.file_url"
                :style="{ height: item.imgThumbH + 'rem' }"
              />
            </div>

            <div
              class="media-name"
              :title="item.name"
              v-if="item.media_type !== '1'"
            >
              {{ item.name }}
            </div>
            <selfAudioPlayer
              v-if="item.media_type === '2'"
              :key="index"
              :path="item.file_url"
            ></selfAudioPlayer>

            <div class="video-box" v-if="item.media_type === '3'">
              <img :src="item.thumbnail_path" alt="" />
              <!--<img src="@/assets/img/afei.jpg" alt="">-->
              <a
                href="javascript:void(0)"
                @click="clickVideoBtn(item)"
                class="play-btn"
              ></a>
            </div>
          </li>
        </ul>
      </div>
      <span class="pre" v-if="imgArr.length > 1" @click="pre"></span>
      <span class="next" v-if="imgArr.length > 1" @click="next"></span>
    </div>

    <div class="picture-looker" v-if="showImgDialog">
      <div class="mask"></div>
      <!--big-->
      <div
        class="picture-main"
        :class="{ big: canEnlarge, out: enlargeStatus === 'out' }"
      >
        <div
          style="display: inline-block;"
          @click="enlarge"
          class="img-main"
          :class="{ in: enlargeStatus === 'in', out: enlargeStatus === 'out' }"
        >
          <img :src="imgArr[clickCurIndex].file_url" />
        </div>
        <!--<img :src="img">-->
      </div>
      <span class="close" @click="showImgDialog = false">
        <img src="@/assets/img/big-close.png" alt="" />
      </span>
      <!--<span class="pre"
                  v-if="imgArr.length>1"
                  @click="bigPre">
                <img src="@/assets/img/big-pre.png" alt="">
            </span>
            <span class="next"
                  v-if="imgArr.length>1"
                  @click="bigNext">
                <img src="@/assets/img/big-next.png" alt="">
            </span>-->
    </div>

    <Dialog
      :title="dialogPlay.title"
      v-if="dialogPlay.visible"
      width="800px"
      @close="closeVideo"
      :visible.sync="dialogPlay.visible"
    >
      <div style="padding:0 10px 15px;">
        <!--video :src="dialogPlay.src"
                       controls
                       style="width: 780px;height: 460px;"
                       autoplay></video>-->
        <VideoPlayer
          :poster="dialogPlay.poster"
          :width="780"
          :height="460"
          :method="playStatus"
          :sources="dialogPlay.src"
        >
        </VideoPlayer>
      </div>
    </Dialog>
  </div>
</template>

<script>
//    let bImg = require('@/assets/img/long.jpg')
//    let bImg = require('@/assets/img/afei.jpg')
//    let bImg2 = require('@/assets/img/shit.jpg')
//    let bImg3 = require('@/assets/img/fuck2.jpg')
import selfAudioPlayer from "@/common/selfAudioPlayer.vue";
export default {
  props: {
    dataSource: {
      type: Array,
    },
    isNeedShowImgName: {
      type: Boolean,
      default: false,
    },
  },
  mounted() {},
  data() {
    return {
      playStatus: "pause",
      dialogPlay: {
        visible: false,
        title: "",
        poster: "",
        src: "",
      },
      imgArr: [],
      cardItemWidth: 17.5,
      cardItemMl: 2.1,
      cardCurIndex: 0,
      clickCurIndex: 0,
      showImgDialog: false,
      canEnlarge: false,
      enlargeStatus: "in", //in:放大图标 out:缩小图标
      imgNativeWidth: 0,
      imgNativeHeight: 0,
      imgCompressWidth: 0,
      imgCompressHeight: 0,
      maxSize: 4, //banner图一屏最多容纳
      //                img:require('@/assets/img/shit.jpg')
      //                img:'https://allen-bigfile.oss-cn-zhangjiakou.aliyuncs.com/shit.jpg'
    };
  },
  watch: {
    dataSource: {
      handler(nv) {
        if (nv && nv.length) {
          this.imgArr = nv.map((item) => {
            return item;
          });
          this.setSmImg();
        }
      },
      immediate: true,
    },
  },
  computed: {},
  methods: {
    clickVideoBtn(item) {
      this.$bus.$emit("playingAudio");
      this.dialogPlay.src = item.file_url;
      this.videoPlay(item);
    },
    videoPlay(item) {
      this.dialogPlay.title = item.name;
      this.dialogPlay.visible = true;
      this.playStatus = "play";
    },
    closeVideo() {
      this.playStatus = "pause";
    },
    setSmImg() {
      let nativeW = 17.5,
        nativeH = 30.67;
      for (let i = 0; i < this.imgArr.length; i++) {
        let item = this.imgArr[i];
        let nImg = new Image();
        nImg.src = this.imgArr[i].file_url;
        nImg.addEventListener("load", () => {
          this.$set(item, "imgNativeWidth", nImg.width);
          this.$set(item, "imgNativeHeight", nImg.height);
          let h = (nImg.height * nativeW) / nImg.width;
          if (h <= nativeH) {
            this.$set(item, "thumbH", nativeH);
            // 1.17是文字的高度。为了保证图片完整显示。所以图面高度需-1.17
            if (this.isNeedShowImgName) {
              let imgNativeH = nativeH - 1.17;
              this.$set(item, "imgThumbH", imgNativeH);
            } else {
              this.$set(item, "imgThumbH", nativeH);
            }
          } else {
            this.$set(item, "thumbH", h);
            if (this.isNeedShowImgName) {
              let imgH = nativeH - 1.17;
              this.$set(item, "imgThumbH", imgH);
            } else {
              this.$set(item, "imgThumbH", h);
            }
          }
        });
      }
      window.console.log(this.imgArr);
    },
    enlarge(e) {
      //                e.stopPropagation()
      if (this.canEnlarge) {
        let img = document.querySelector(".img-main img");
        if (this.enlargeStatus === "in") {
          img.style.width = this.imgNativeWidth + "px";
          img.style.height = this.imgNativeHeight + "px";
        } else {
          img.style.width = this.imgCompressWidth + "px";
          img.style.height = this.imgCompressHeight + "px";
        }
        this.enlargeStatus = this.enlargeStatus === "in" ? "out" : "in";
        window.console.log(
          this.imgNativeWidth,
          this.imgNativeHeight,
          this.imgCompressWidth,
          this.imgCompressHeight
        );
      }
    },
    //1:pre 2:next
    move(type) {
      //第一张和最后一张不要再操作
      if (type === 1) {
        if (this.cardCurIndex === 0) {
          return;
        }
        this.cardCurIndex--;
      } else {
        if (this.cardCurIndex >= this.imgArr.length - this.maxSize) {
          return;
        }
        this.cardCurIndex++;
      }
      window.console.log(this.cardCurIndex);
      let studyCard = document.querySelector(".study-card-list");
      let d = this.cardItemWidth + this.cardItemMl;
      let distance = -d;
      studyCard.style.marginLeft = distance * this.cardCurIndex + "rem";
    },
    bigMove(type) {
      //第一张和最后一张不要再操作
      if (type === 1) {
        if (this.clickCurIndex === 0) {
          return;
        }
        this.clickCurIndex--;
      } else {
        if (this.clickCurIndex >= this.imgArr.length - 1) {
          return;
        }
        this.clickCurIndex++;
      }
      this.look(this.imgArr[this.clickCurIndex].path, this.clickCurIndex);
    },
    pre() {
      this.move(1);
    },
    next() {
      this.move(2);
    },
    bigPre() {
      this.bigMove(1);
    },
    bigNext() {
      this.bigMove(2);
    },
    look(img, index, mediaType) {
      if (mediaType !== "1") {
        return;
      }
      //                e.stopPropagation()
      //                window.open(img,'_blank')
      //                return
      this.clickCurIndex = index;
      this.showImgDialog = true;
      let nImg = new Image();
      //                nImg.src = img
      nImg.src = img;
      let root = document.documentElement || document.body;
      let rootW = root.clientWidth;
      let rootH = root.clientHeight;
      this.canEnlarge = false;
      this.enlargeStatus = "in";
      nImg.addEventListener("load", () => {
        let imgW = nImg.width;
        let imgH = nImg.height;
        let bigPic;
        this.imgNativeWidth = imgW;
        this.imgNativeHeight = imgH;
        //缩放规则：
        //如果（原图得宽度和窗口得宽度差）>（原图得高度和窗口得高度差）用宽度比利来缩放
        //如果（原图得宽度和窗口得宽度差） <（原图得高度和窗口得高度差）用高度比利来缩放
        if (imgW >= rootW || imgH >= rootH) {
          let w = imgW - rootW;
          let h = imgH - rootH;
          let bili, compressW, compressH;
          if (w > h) {
            compressW = rootW * 0.9;
            bili = compressW / imgW;
            compressH = imgH * bili;
            bigPic = document.querySelector(".picture-main img");
            bigPic.style.width = compressW + "px";
            bigPic.style.height = compressH + "px";
            this.imgCompressWidth = compressW;
            this.imgCompressHeight = compressH;
          } else {
            compressH = rootH * 0.9;
            bili = compressH / imgH;
            compressW = imgW * bili;
            bigPic = document.querySelector(".picture-main img");
            bigPic.style.width = compressW + "px";
            bigPic.style.height = compressH + "px";
            this.imgCompressWidth = compressW;
            this.imgCompressHeight = compressH;
          }
          this.canEnlarge = true;
        } else {
          bigPic = document.querySelector(".picture-main img");
          bigPic.style.width = imgW + "px";
          bigPic.style.height = imgH + "px";
          this.imgCompressWidth = imgW;
          this.imgCompressHeight = imgH;
        }
        //                    window.console.log(nImg.width);
      });
    },
  },
  components: {
    selfAudioPlayer,
  },
};
</script>
<style lang="postcss" scoped>
.study-card-list {
  transition: margin-left 1s;
}
</style>
