<template>
  <div class="main-center" v-loading="submitAble">
    <div class="courese-index-box">
      <ul class="course-search-list">
        <li class="courstatus-li">
          <el-select
            v-model="lessonParams.course_status"
            @change="changeLessonStatus"
            placeholder="选择上课状态"
            :style="lessonParams.course_status==='0'||lessonParams.course_status==='1'||lessonParams.course_status==='2'?{'width':'8.2rem'}:{'width':'11.7rem'}"
          >
            <el-option
              v-for="item in lessonStatusList"
              :key="item.id"
              :label="item.name"
              :value="item.id"
            >
            </el-option>
          </el-select>
        </li>
        <li>
          <course-list v-model="classIds" :default-all-select="isAllSelectCourse"></course-list>
        </li>
        <li class="course-date-picker">
          <el-date-picker
            v-model="dateArr"
            @change="changeDate"
            prefix-icon="false"
            size="small"
            type="daterange"
            range-separator="-"
            start-placeholder="请选择上课开始日期"
            end-placeholder="请选择上课结束日期"
          >
          </el-date-picker>
        </li>
      </ul>
      <ul class="course-list" v-if="lessonList.length">
        <li v-for="item in lessonList" :key="item.lesson_id">
            <div class="course-box">
                <div class="left-content">
                    <div class="left-header">
                        <div class="tip" :style="{'--color':(item.course_status==='1'?'#FF5A00':'#FFBA01')}" v-if="item.course_status!=='2'">{{item.course_status==='0'?'未上课':item.course_status==='1'?'上课中':'已下课'}}</div>
                        <div class="lesson-name">{{item.arrangement_name}}</div>
                    </div>
                    <div class="course-name">{{item.course_name}}</div>
                    <div class="course-time">
                      <img src="@/assets/img/time-ico.png" alt="">
                      {{item.start_time}}
                    </div>
                    <div class="address">
                        <img src="@/assets/img/address-ico.png" alt="">
                        <p>
                            {{item.is_classin==='0'?'线下课':'线上课'}}
                            <span class="dot"></span>
                            {{item.is_classin==='0'?item.teaching_location_name:'Classin教室'}}
                        </p>
                    </div>
                </div>
                <div class="right-content">
                    <div class="teacher-content" v-if="item.execute_teacher_name!=''">
                        <img :src="item.execute_teacher_head_url">
                        <p>{{item.execute_teacher_name}}</p>
                    </div>
                    <div class="button-content" :style="item.execute_teacher_name==''?{'margin-top':'6.25rem'}:{}">
                        <button @click="goCourseWork(item,'1')" :style="item.isBeforeHaveTask===true?{'background': '#FF7D30'}:{'background': '#D9D9D9'}">课前资源</button>
                        <button @click="goCourseWork(item,'2')" :style="item.isAfterHaveTask===true?{'background': '#FF7D30'}:{'background': '#D9D9D9'}">课后资源</button>
                    </div>
                </div>
            </div>
        </li>
      </ul>
      <div class="flex-box-center" v-if="lessonList.length">
        <el-pagination
          background
          :page-size.sync="lessonParams.page_size"
          :current-page.sync="lessonParams.page_num"
          layout="prev, pager, next"
          @current-change="getLessonList"
          :total="total"
        >
        </el-pagination>
      </div>
    </div>
    <div class="has-not-child-box" v-if="!lessonList.length">
      <img src="@/assets/img/empty-img.png" alt="" />
      <p class="login-tit">暂无课次～</p>
    </div>
  </div>
</template>

<script>
import { mapActions, mapMutations } from "vuex";
import courseList from "@/common/courseList.vue";
export default {
  data: () => ({
    classIds: [],
    dateArr: [],
    total: 0,
    lessonParams: {
      class_id_list: [],
      course_status: "0", //1, "即将上课"2, "正在上课"3, "已结束"
      page_num: 1,
      page_size: 5,
      begin_school_time: "",
      end_school_time: "",
    },
    courseList: [],
    lessonList: [],
    lessonStatusList: [
      { name: "未上课", id: "0" },
      { name: "上课中", id: "1" },
      { name: "已下课", id: "2" }
    ],
    submitAble: false,
    isAllSelectCourse: true
  }),
  methods: {
    ...mapActions("course", ["getLessonListActions"]),
    ...mapMutations(["updateCourseSelectList", "updateCourseStatus","updateCoursePage"]),
    goCourseWork(item,type){
        if(item.isBeforeHaveTask===false&&type==='1'){
            this.$message.warning('老师没有布置课前资源哦~')
            return
        }
        if(item.isAfterHaveTask===false&&type==='2'){
            this.$message.warning('老师没有布置课后资源哦~')
            return
        }
           this.goView('courseStudyTypeList', {arrangement_id: item.arrangement_id,task_classify:type})
    },
    changeDate(value) {
      if (value != null && value.length > 0) {
        this.lessonParams.begin_school_time =
          new Date(this.formatDate(value[0]) + " 00:00:00").getTime() / 1000;
        this.lessonParams.end_school_time =
          new Date(this.formatDate(value[1]) + " 23:59:59").getTime() / 1000;
      } else {
        this.lessonParams.begin_school_time = "";
        this.lessonParams.end_school_time = "";
      }
      this.lessonParams.page_num = 1;
      this.getLessonList();
    },
    formatDate(value) {
      let date = new Date(value);
      let year = date.getFullYear();
      let month = date.getMonth() + 1;
      let day = date.getDate();
      return year + "/" + month + "/" + day;
    },
    createLessonParams() {
      let oldParams = JSON.parse(JSON.stringify(this.lessonParams));
      let params = {};
      if (this.classIds !== undefined && this.classIds !== "") {
        oldParams.course_id_list = this.classIds;
      } else {
        oldParams.course_id_list = undefined;
      }
      for (let key in oldParams) {
        if (oldParams.hasOwnProperty(key)) {
          if (!this.valueIsNull(oldParams[key])) {
            params[key] = oldParams[key];
          }
        }
      }
      return params;
    },
    getLessonList(method) {
      let params = this.createLessonParams();
      /* 为了初始显示完成状态 */
      switch (params.course_status) {
        case undefined:
          params.course_status = "0";
          break;
        case "0":
          params.course_status = "0";
          break;
        case "1":
          params.course_status = "1";
          break;
        case "2":
          params.course_status = "2";
          break;
      }
      this.submitAble = true;
      this.getLessonListActions(params).then(
        (res) => {
          this.submitAble = false;
          let { error_code, data } = res;
          if (!error_code) {
            this.lessonList = data.rows;
            this.total = data.total;
          }
        },
        () => {
          this.submitAble = false;
        }
      );
    },
    changeLessonStatus() {
      this.lessonParams.page_num = 1;
      this.getLessonList();
    }
  },
  watch: {
    classIds: {
      handler(nv) {
        if(this.$store.state.course_page>1){
          this.lessonParams.page_num = this.$store.state.course_page;
        }else {
          this.lessonParams.page_num = 1;
        }
        this.getLessonList();
      },
      deep: true,
    },
  },
  beforeRouteLeave(to, from, next) {
    this.updateCourseSelectList(this.classIds);
    this.updateCourseStatus(this.lessonParams.course_status);
    this.updateCoursePage(this.lessonParams.page_num)
    next();
  },
  mounted() {
    this.classIds = this.$store.state.course_select_list
    if(this.$store.state.course_status){
      this.lessonParams.course_status = this.$store.state.course_status;
    }
  },
  computed: {},
  components: {
    courseList,
  },
  created() {},
};
</script>

<style lang="postcss" scoped>
.main-center {
  & .course-search-list {
    & li {
      margin-right: 0;
    }
  }
}
</style>
