    <Dialog
      :title="dialogPlay.title"
      v-if="dialogPlay.visible"
      width="800px"
      @close="closeVideo"
      :visible.sync="dialogPlay.visible"
    >
      <div style="padding:0 10px 15px;">
        <VideoPlayer
          :poster="dialogPlay.poster"
          :width="780"
          :height="460"
          :method="playStatus"
          :sources="dialogPlay.src"
        >
        </VideoPlayer>
        <div class="change-video-box">
          <span class="tit">回放选集</span>
          <ul class="change-video-btn-list">
            <li
              :key="index"
              @click="tabVideoUrl(index)"
              :class="{ current: currentVedioIdx === index }"
              v-for="(item, index) in videoList"
            >
              {{ index + 1 }}
            </li>
          </ul>
        </div>
      </div>
    </Dialog>
