<script>
    function getCommonObjFrame() {
        return {
            message: '',
            task_content: {
                star_num: 0,
                knowledge_score_message: []
            }
        }
    }

    function getAssessmentObjFrame() {
        return {
            enable: '0',
            message: '',
            assessment_content: {
                assessment_result: [],
                assessment_template_content: []
            },
            assessment_template_list: []
        }
    }

    import {mapActions} from 'vuex'
    import star from '@/common/star.vue'
    import studyCard from '@/common/studyCard.vue'

    export default {
        data: () => ({
            /*rules: {
                name: [
                    { required: true, message: '请输入姓名', trigger: 'blur' },
                    { min: 3, max: 5, message: '长度在 3 到 5 个字符', trigger: 'blur' }
                ],
                age:[
                    {required: true, message: '请输入年龄', trigger: 'blur'},
                    {pattern:/^\d$/,message:'年龄必须为数字', trigger: 'blur'}
                ]
            },
            formModel:{

            },*/
            submitAble: false,
            playStatus: 'pause',
            videoList: [],
            currentVedioIdx: 0,
            dialogPlay: {
                visible: false,
                title: '课节回放',
                poster: '',
                src: ''
//                poster:require('@/assets/img/video-img.jpg'),
//                src:require('@/assets/videos/jay.mp4')
            },
            hasPreview: false,//是否显示
            hasReview: false,//是否显示课后复习
            hasAssessment: false,//是否显示评价
            lessonDetail: {
                assessment: getAssessmentObjFrame(),
                preview: getCommonObjFrame(),
                review: getCommonObjFrame()
            },
            assessmentType: 1,//1:中文 2:英文
//            studyCards:[require('@/assets/img/long.jpg')]
            studyCards: [],
            previewSrc: 'ico-preview',
            replaySrc: 'ico-replay',
            homeworkSrc: 'ico-homework'

        }),
        methods: {
            /*fn(){
                console.log(this.$refs.myForm,33333);
                let form = this.$refs.myForm
                form.fields[0].$el.focus()
            },*/
            /* task_classify 1课前预习 2课后练习 */
            /* type preview或者review */
            goViewFn(detail,type,task_classify) {
                if(type.enable==='0'||type.has_task==='0'){
                    this.$message.warning(type.warn)
                    return
                }else{
                    this.goView('courseStudyTypeList', {lesson_id: detail.lesson_id,task_classify:task_classify})
                }
            },
            resetForm() {
                this.$refs['myForm'].resetFields();
            },
            ...mapActions('course', ['getLessonDetailActions', 'getLessonReplayActions', 'getStudyReportWebViewInputDataAction']),
            //type 1:mouserover  2.mouseout
            hoverAssessment(e, item, index, type) {
                e.stopPropagation()
                if (!item.hasMore) {
                    return
                }
                if (type === 1) {
                    item.hover = true
                } else {
                    item.hover = false
                }
//                window.console.log(item,index,type)
            },
            tabVideoUrl(index) {
                if (this.currentVedioIdx === index) {
                    return
                }
                this.currentVedioIdx = index
                this.dialogPlay.src = this.videoList[index]
                this.playStatus = 'pause'
                setTimeout(() => {
                    this.playStatus = 'play'
                })
            },
            closeVideo() {
                this.playStatus = 'pause'
            },
            videoPlay() {
                this.dialogPlay.visible = true
                this.playStatus = 'play'
            },
            playLesson() {
                if (this.submitAble) {
                    return
                }
                let _this = this
                let params = {
                    lesson_id: _this.lessonDetail.lesson_id
//                    lesson_id:'1'
                }
                if (!this.videoList.length) {
                    this.getLessonReplayActions(params).then((res) => {
                        this.submitAble = false
                        let {error_code, data} = res;
                        if (!error_code) {
                            this.videoList = data.replay_url_list
//                            this.videoList[1] = 'https://allen-bigfile.oss-cn-zhangjiakou.aliyuncs.com/jay2.mp4'
                            this.dialogPlay.poster = data.cover_image
                            if (this.videoList.length) {
                                this.dialogPlay.src = this.videoList[0]
                                this.currentVedioIdx = 0
                                this.videoPlay()
                            }
                        }
                    }, () => {
                        this.submitAble = false
                    })
                } else {
                    this.videoPlay()
                }
            },
            //            获取ABC结果背景图
            getResultStyle(index) {
                let imgMapper = {
                    0: 'red',
                    1: 'yellow',
                    2: 'blue'
                }
                let imgPath = require(`@/assets/img/result-bg-${imgMapper[index]}-2.png`)
                return {
                    backgroundImage: `url('${imgPath}')`
                }
            },
            //获取ABC字体颜色
            getResultTextStyle(index) {
                let colorMapper = {
                    0: '#F46262',
                    1: '#FFC31D',
                    2: '#69B0EA'
                }
                let color = colorMapper[index]
                return {
                    color: color
                }
            },
            translateAssessment() {
                if (this.assessmentType === 1) {
                    this.assessmentType = 2
                } else {
                    this.assessmentType = 1
                }
                this.setAssessmentOverflow()
            },
            getLessonDetail() {
                let lesson_id = this.$route.query.lesson_id
                let params = {
                    lesson_id: lesson_id
                }
                this.getLessonDetailActions(params).then((res) => {
                    let {error_code, data} = res;
                    if (!error_code) {
                        this.lessonDetail = data
                        /*let url = require('@/assets/img/afei.jpg')
                        while(this.lessonDetail.learning_card_list.length<7){
                            this.lessonDetail.learning_card_list.push({
                                card_image_url:url
                            })
                        }*/
//                        this.lessonDetail.learning_card_list[4] = this.lessonDetail.learning_card_list[2]
//                        this.lessonDetail.learning_card_list[5] = this.lessonDetail.learning_card_list[3]
                        if (this.lessonDetail.learning_card_list&&this.lessonDetail.learning_card_list.length) {
                            this.studyCards = this.lessonDetail.learning_card_list.map((item) => {
                                return item
                            })
                        }
                        this.setSomeUi()
                        this.getIco(data)
                        /*this.lessonDetail.assessment.assessment_content.assessment_template_content[0].options.push({
                            english_option_content: "Student is able to recognize the word in both written and spoken forms. Student is able to use words and sentence patterns they have learned in conversations and activities with teachers and peers.",
                            option_content: "学生能在文本或对话中读懂/听懂所学单词。在课堂对话或和老师同学的课堂互动中，正确使用所学的单词或句型。",
                            option_key: "D"
                        })*/
                        window.console.log(this.lessonDetail, 'detail!!~~')
                        this.setAssessmentOverflow()
                    }
                })
            },
            setAssessmentOverflow() {
                let _this = this
                let assessment = this.lessonDetail.assessment.assessment_content
                if (assessment && assessment.assessment_result && assessment.assessment_result.length) {
                    assessment.assessment_result.forEach((item) => {
                        item.hasMore = false
                    })
                    setAssessmentOverflow(assessment.assessment_result)
                }

                function setAssessmentOverflow(assResult) {
                    _this.$nextTick(() => {
                        let assessmentList = document.getElementById('assessment-list')
                        let assessmentConBox = assessmentList.querySelector('.assessment-con-box')
                        let assessmentConMains = assessmentList.querySelectorAll('.assessment-con-main')
                        let h = assessmentConBox.offsetHeight
//                    window.console.log(assessmentConBox.offsetHeight,'777');
//                    window.console.log(assessmentConMains.length,'888');
                        Array.from(assessmentConMains).forEach((item, index) => {
                            let itemH = item.offsetHeight
                            if (itemH > h) {
                                assResult[index].hasMore = true
//                            window.console.log(itemH,index,'999');
                            }
                        })
                    })
                }
            },
            //设置课前练习，课后练习是否禁用,设置课前练习，课后练习是否完成
            setSomeUi() {
                let detail = this.lessonDetail
                //防对象为undefined做的一些初始化
                if (!detail.preview) {
                    this.hasPreview = false
                    detail.preview = getCommonObjFrame()
                } else {
                    this.hasPreview = true
                    if (!detail.preview.task_content) {
                        detail.preview.task_content = {
                            star_num: 0,
                            knowledge_score_message: []
                        }
                    }
                }

                if (!detail.review) {
                    this.hasReview = false
                    detail.review = getCommonObjFrame()
                } else {
                    this.hasReview = true
                    if (!detail.review.task_content) {
                        detail.review.task_content = {
                            star_num: 0,
                            knowledge_score_message: []
                        }
                    }
                }


                if (!detail.assessment || !detail.assessment.assessment_content) {
                    let msg = ''
                    if (!detail.assessment) {
                        this.hasAssessment = false
                    } else {
                        this.hasAssessment = true
                        msg = detail.assessment.message
                    }
                    detail.assessment = getAssessmentObjFrame()
                    detail.assessment.message = msg
                } else {
                    this.hasAssessment = true
                }
                //防对象为undefined做的一些初始化

                detail.preview.task_content.star_num = Number(detail.preview.task_content.star_num)
                detail.review.task_content.star_num = Number(detail.review.task_content.star_num)

//                detail.preview.task_content.knowledge_score_message[2].user_star_num = 2

                let previewDisabled = this.btnDisabled(detail.preview)
                let reviewDisabled = this.btnDisabled(detail.review)
                let previewNotComplete = this.isNotComplete(detail.preview, previewDisabled)
                let reviewNotComplete = this.isNotComplete(detail.review, reviewDisabled)
                this.$set(detail, 'previewDisabled', previewDisabled)
                this.$set(detail, 'reviewDisabled', reviewDisabled)
                this.$set(detail, 'previewNotComplete', previewNotComplete)
                this.$set(detail, 'reviewNotComplete', reviewNotComplete)

                let assResult = detail.assessment.assessment_content.assessment_result
                if (assResult.length) {
                    assResult.forEach((item) => {
                        this.$set(item, 'hasMore', false)
                        this.$set(item, 'hover', false)
                    })
                }
            },
            isNotComplete(obj, btnDisabled) {
                let notComplete = false
                if (btnDisabled) {
                    notComplete = false
                } else {
                    notComplete = (obj.is_complete == 0 ? true : false)
                }
                return notComplete
            },
            btnDisabled(obj) {
                let disabled = true
                if (obj.enable !== undefined) {
                    disabled = (obj.enable == 0 ? true : false)
                } else {
                    disabled = true
                }
                return disabled
            },
            //根据返回对象决定ico图片路径
            getIco(obj) {
                let { preview, replay, review } = obj
                if(preview){
                    if(preview.enable==='1'){
                        if(preview.has_task==='1'){
                             this.previewSrc = 'ico-preview'
                        }else{
                             this.previewSrc = 'ico-no-preview'
                        }
                    }else {
                        this.previewSrc = 'ico-lock-preview'
                    }
                }
                if(replay){
                    if(replay.enable==='1'){
                        if(replay.has_replay==='1'){
                             this.replaySrc = 'ico-replay'
                        }else{
                             this.replaySrc = 'ico-no-replay'
                        }
                    }else {
                        this.replaySrc = 'ico-lock-replay'
                    }
                }
                if(review){
                    if(review.enable==='1'){
                        if(review.has_task==='1'){
                             this.homeworkSrc = 'ico-homework'
                        }else{
                             this.homeworkSrc = 'ico-no-homework'
                        }
                    }else {
                        this.homeworkSrc = 'ico-lock-homework'
                    }
                }
            },
            getLearnReportUrl(lesssonId){
                let lesson_id = this.$route.query.lesson_id;
                let params = {
                    lesson_id: lesson_id,
                };
                this.getStudyReportWebViewInputDataAction(params).then((res) => {
                     let { error_code, data } = res;
                     if (!error_code) {
                        let str = this.getQueryVariable(data.complete_web_view_url,'c')
                        if(str){
                            this.goView('learnReport',{c:str})
                        }
                     }
                });
            },
            getQueryVariable(url,variable) {
                let query = url.substring(1);
                     let vars = query.split("&");
                     for (var i=0;i<vars.length;i++) {
                         var pair = vars[i].split("=");
                         if(pair[0] == variable){return pair[1];}}
                return(false);
            }
        },
        watch: {},
        mounted() {
            this.getLessonDetail()
        },
        computed: {
//            playStatus() {
//                let self = this;
//                let method = self.dialogPlay.visible ? 'play': 'pause';
//                if(method === 'play' && self.isDialog){
//                    method = self.$parent.visible ? 'play': 'pause';
//                }
//                if(method !== 'play'){
//                    self.dialogPlay.visible = false;
//                }
//                return method;
//            },
            isDialog() {
                let self = this;
                let $parent = self.$parent;
                let className = ($parent.$el && $parent.$el.className) || "";
                return (className.indexOf('el-dialog__wrapper') !== -1);
            }
        },
        components: {
            //star
            //studyCard
        },
        created() {
        }
    }
</script>

