<template>
  <div class="study-page-list">
    <ul class="study-card-list">
      <li v-for="(item, index) in imgArr" style="border:none" :class="{ media: item.media_url !== '1' }" :key="index">
        <!-- @click="look(item.media_url, index, item.media_type)" -->
        <!--media_type 1.图片 2.音频 3.视频-->
        <div v-if="item.media_type === '1'" @click="look(item.media_url, index, item.media_type)">
          <!--v-if="isNeedShowImgName"-->
          <div style="width: 100%;margin-bottom: 10px;color:#666"
               class="img-name">{{ item.media_name }}</div>
          <img v-if="item.imgThumbW||item.imgThumbH" :src="item.media_url" :style="{width: item.imgThumbW, height: item.imgThumbH }" />
        </div>
        <div class="media-box" v-if="item.media_type === '2'">
          <selfAudioPlayer
            :key="index"
            :height="460"
            :path="item.media_url"
            :is-modal="true"
            :have-title="true"
          ></selfAudioPlayer>
          <div class="video-title">
            {{item.media_name}}
          </div>
        </div>
       <div class="media-box" v-if="item.media_type === '3'">
        <div class="video-box" style="margin-top:0rem;">
          <img v-if="!isShowVideo" :src="item.thumbnail_url" alt="" />
          <a
            v-if="!isShowVideo"
            href="javascript:void(0)"
            @click="clickVideoBtn(item)"
            class="play-btn"
          ></a>
          <VideoPlayer
            v-if="isShowVideo"
            :method="playStatus"
            :width="50.84"
            :height="28.67"
            :util="unil"
            :sources="item.media_url"
          >
          </VideoPlayer>
        </div>
        <div class="video-title" style="height: 2rem;font-size: 1.5rem;font-family: MicrosoftYaHei;color: #565656;line-height: 1.83rem;margin-top: 3.67rem;">
            {{item.media_name}}
          </div>
       </div>
        <div v-if="item.media_type === '7'" @click="lookPdf(item.media_url)" style="text-align: center">
          <div class="img-name" style="width: 100%;margin-bottom: 10px;color:#666">{{ item.media_name }}</div>
          <img :src="item.thumbnail_url"
               style="max-width: 100%;height: auto;width: auto"/>
        </div>
      </li>
    </ul>
    <div class="picture-looker" v-if="showImgDialog">
      <div class="mask"></div>
      <!--big-->
      <div
        class="picture-main"
        :class="{ big: canEnlarge, out: enlargeStatus === 'out' }"
      >
        <div
          style="display: inline-block;"
          @click="enlarge"
          class="img-main"
          :class="{ in: enlargeStatus === 'in', out: enlargeStatus === 'out' }"
        >
          <div class="img-name">{{ imgArr[clickCurIndex].name }}</div>
          <img :src="imgArr[clickCurIndex].media_url" />
        </div>
        <!--<img :src="img">-->
      </div>
      <span class="close" @click="showImgDialog = false">
        <img src="@/assets/img/big-close.png" alt="" />
      </span>

    </div>
  </div>
</template>

<script>
import selfAudioPlayer from "@/common/selfAudioPlayer.vue";
import helper from '@/assets/js/helper'
// import VideoJS from "video.js";
// import VideoJSFlash from "videojs-flash";
// import VideoJSHLS from "videojs-contrib-hls";
// import "video.js/dist/video-js.min.css";
export default {
  props: {
    dataSource: {
      type: Array,
    },
    isNeedShowImgName: {
      type: Boolean,
      default: false,
    },
    isShowVideo: {
      type: Boolean,
    },
  },
  mounted() {},
  data() {
    return {
      playStatus: "pause",
      unil: "rem",
      dialogPlay: {
        visible: false,
        title: "",
        poster: "",
        src: "",
      },
      imgArr: [],
      cardItemWidth: 17.5,
      cardItemMl: 2.1,
      cardCurIndex: 0,
      clickCurIndex: 0,
      showImgDialog: false,
      canEnlarge: false,
      enlargeStatus: "in", //in:放大图标 out:缩小图标
      imgNativeWidth: 0,
      imgNativeHeight: 0,
      imgCompressWidth: 0,
      imgCompressHeight: 0,
      maxSize: 4, //banner图一屏最多容纳
    };
  },
  watch: {
    dataSource: {
      handler(nv) {
        if (nv && nv.length) {
          this.imgArr = nv.map((item) => {
            return item;
          });
          this.setSmImg();
        }
      },
      immediate: true,
    },
  },
  computed: {},
  methods: {
    clickVideoBtn(item) {
      this.isShowVideo = true;
    },
    setSmImg() {
      let nativeW = 26.08*12,
        nativeH = 46.42*12,
        w = 26.28,
        h = 46.42;
      for (let i = 0; i < this.imgArr.length; i++) {
        let item = this.imgArr[i];
        let nImg = new Image();
        let src = item.media_type === '7' ? this.imgArr[i].thumbnail_url : this.imgArr[i].media_url;
//        item.thumbnail_url = require('@/assets/img/biancheng-bg.jpg')
//        item.thumbnail_url = 'http://dev-images.thedeer.cn/course/f5b06f83fc9bff475b6e9b584b406a61j19a29vqh8hq3pzw.png-thumbnail_240'
//        let src = item.media_type === '7' ? item.thumbnail_url : this.imgArr[i].media_url;
        nImg.src = src;
        nImg.addEventListener("load", () => {
          if(nImg.height>=nativeH){
            this.$set(item, "imgThumbW", 'auto')
            this.$set(item, "imgThumbH", `${h}rem`)
          }else {
            this.$set(item, "imgThumbW", 'auto')
            this.$set(item, "imgThumbH", `${nImg.height/12}rem`)
          }
          if(nImg.height<nativeH&&nImg.width<nativeW){
            this.$set(item, "imgThumbW", `${nImg.width/12}rem`)
            this.$set(item, "imgThumbH", `${nImg.height/12}rem`)
          }
        });
      }
    },
    enlarge(e) {
      //                e.stopPropagation()
      if (this.canEnlarge) {
        let img = document.querySelector(".img-main img");
        if (this.enlargeStatus === "in") {
          img.style.width = this.imgNativeWidth + "px";
          img.style.height = this.imgNativeHeight + "px";
        } else {
          img.style.width = this.imgCompressWidth + "px";
          img.style.height = this.imgCompressHeight + "px";
        }
        this.enlargeStatus = this.enlargeStatus === "in" ? "out" : "in";
      }
    },
    //1:pre 2:next
    move(type) {
      //第一张和最后一张不要再操作
      if (type === 1) {
        if (this.cardCurIndex === 0) {
          return;
        }
        this.cardCurIndex--;
      } else {
        if (this.cardCurIndex >= this.imgArr.length - this.maxSize) {
          return;
        }
        this.cardCurIndex++;
      }
      let studyCard = document.querySelector(".study-card-list");
      let d = this.cardItemWidth + this.cardItemMl;
      let distance = -d;
      studyCard.style.marginLeft = distance * this.cardCurIndex + "rem";
    },
    bigMove(type) {
      //第一张和最后一张不要再操作
      if (type === 1) {
        if (this.clickCurIndex === 0) {
          return;
        }
        this.clickCurIndex--;
      } else {
        if (this.clickCurIndex >= this.imgArr.length - 1) {
          return;
        }
        this.clickCurIndex++;
      }
      this.look(this.imgArr[this.clickCurIndex].path, this.clickCurIndex);
    },
    pre() {
      this.move(1);
    },
    next() {
      this.move(2);
    },
    bigPre() {
      this.bigMove(1);
    },
    bigNext() {
      this.bigMove(2);
    },
    lookPdf(url){
      let u = url + '#toolbar=0'
      helper.windowOpen(u)
    },
    look(img, index, mediaType) {
      if (mediaType !== "1") {
        return;
      }
      this.clickCurIndex = index;
      this.showImgDialog = true;
      let nImg = new Image();
      //                nImg.src = img
      nImg.src = img;
      let root = document.documentElement || document.body;
      let rootW = root.clientWidth;
      let rootH = root.clientHeight;
      this.canEnlarge = false;
      this.enlargeStatus = "in";
      nImg.addEventListener("load", () => {
        let imgW = nImg.width;
        let imgH = nImg.height;
        this.imgNativeWidth = imgW;
        this.imgNativeHeight = imgH;
        //缩放规则：
        //如果（原图得宽度和窗口得宽度差）>（原图得高度和窗口得高度差）用宽度比利来缩放
        //如果（原图得宽度和窗口得宽度差） <（原图得高度和窗口得高度差）用高度比利来缩放
        if (imgW >= rootW || imgH >= rootH) {
          let w = imgW - rootW;
          let h = imgH - rootH;
          let bili, compressW, compressH, bigPic;
          if (w > h) {
            compressW = rootW * 0.9;
            bili = compressW / imgW;
            compressH = imgH * bili;
            bigPic = document.querySelector(".picture-main img");
            bigPic.style.width = compressW + "px";
            bigPic.style.height = compressH + "px";
            this.imgCompressWidth = compressW;
            this.imgCompressHeight = compressH;
          } else {
            compressH = rootH * 0.9;
            bili = compressH / imgH;
            compressW = imgW * bili;
            bigPic = document.querySelector(".picture-main img");
            bigPic.style.width = compressW + "px";
            bigPic.style.height = compressH + "px";
            this.imgCompressWidth = compressW;
            this.imgCompressHeight = compressH;
          }
          this.canEnlarge = true;
        }
        //                    window.console.log(nImg.width);
      });
    },
  },
  components: {
    selfAudioPlayer,
  },
};
</script>
<style lang="postcss" scoped>
.study-card-list {
  transition: margin-left 1s;
      overflow: scroll;
}
.video-title {
  height: 2rem;
  font-size: 1.5rem;
  font-family: MicrosoftYaHei;
  color: #565656;
  line-height: 1.83rem;
  margin-top: 4.98rem;
  text-align: center;
}
.media-box {
  width: 54.17rem;
  height: 39.83rem;
  padding: 1.67rem 1.67rem 0 1.67rem;
  box-shadow: 0rem 0.17rem 0rem 0rem rgba(0, 0, 0, 0.03);
  border-radius: 1.67rem;
  border: 0.08rem solid #E8E8E8;
}
</style>
