<template>
  <div class="main-center" v-loading="submitAble">
    <div class="read-list-box">
      <div class="wrap-nav">
        <a href="javascript:void(0)" @click="goView('courseIndex')">我的课程</a>&gt;
        <a href="javascript:void(0)" @click="goView('courseStudyTypeList', {arrangement_id: $route.query.arrangement_id,task_classify: $route.query.task_classify,})">{{ $route.query.task_classify === "1" ? "课前资源" : "课后资源" }}</a>&gt;
        <a href="javascript:void(0)" class="current">绘本听读</a>
      </div>
      <ul>
      <li v-for="(item, index) in bookTestList" :key="index" @click="goTest(item)">
          <img :src="item.img_url" alt="" />
          <div class="book-box-footer">
            <span class="book-name">{{ item.chapter_name }}</span>
            <span class="finish">
              已读<span>{{ item.complete_total_int }}</span>天
            </span>
          </div>
        </li>
      </ul>
    </div>
  </div>
</template>

<script>
import { mapActions } from "vuex";
export default {
  data: () => ({
    bookTestList: [],
  }),
  methods: {
    ...mapActions("course", ["getTaskListByBookActions","getTaskGoToUrlActions"]),
    getTaskBookList() {
      let params = {
        course_arrangement_id: this.$route.query.arrangement_id,
        task_classify: this.$route.query.task_classify
      };
      this.getTaskListByBookActions(params).then((res) => {
        let { error_code, data } = res;
        if (!error_code) {
          this.bookTestList = data;
        }
      });
    },
    goTest(item) {
      let params = {
        student_task_id: item.student_task_id
      }
      this.getTaskGoToUrlActions(params).then((res)=>{
        let {error_code,data} = res;
          if(!error_code){
            if(data.webPath){
              this.goView('bookTest',{
                webUrl:data.webPath,
                from: 'readTestList',
                arrangement_id: this.$route.query.arrangement_id,
                task_classify: this.$route.query.task_classify,
              })
            }
          }
      })
    },
  },
  watch: {},
  mounted() {
    this.getTaskBookList();
  },
  computed: {},
  components: {},
  created() {},
};
</script>

<style lang="postcss" scoped>
.main-center {
  & .read-list-box {
    padding: 2.5rem 2.5rem;
    & ul {
      margin-top:1.67rem;
      display: flex;
      flex-direction: row;
      flex-wrap: wrap;
      & li {
        cursor: pointer;
        width: 19.83rem;
        height: 19.5rem;
        background: #ffffff;
        box-shadow: 0rem 0.17rem 0rem 0rem rgba(0, 0, 0, 0.03);
        border-radius: 0.42rem;
        border: 0.08rem solid #e8e8e8;
        &:not(:nth-child(3n + 1)) {
          margin-left: 1rem;
        }
        &:nth-of-type(n+4){
          margin-top: 1.25rem;
        }
        & img {
          width: 17.33rem;
          height: 14.33rem;
          margin-left: 1.1rem;
          margin-top:1.1rem;
          image-rendering: crisp-edges;
          image-rendering: -moz-crisp-edges;
          image-rendering: -o-crisp-edges;
          image-rendering: -webkit-optimize-contrast;
          -ms-interpolation-mode: nearest-neighbor;
        }
        & .book-box-footer {
          margin-top: 1.25rem;
          position: relative;
          & .book-name {
            position: absolute;
            left: 1.25rem;
            height: 1.42rem;
            font-size: 1.17rem;
            font-family: MicrosoftYaHei;
            color: #222222;
            line-height: 1.42rem;
          }
          & .finish {
            position: absolute;
            height: 1.17rem;
            font-size: 1rem;
            font-family: MicrosoftYaHei;
            color: #ADADAD;
            line-height: 1.17rem;
            right: 1.25rem;
            & span {
              color: #FF8339;
            }
            & img {
              width: 1.05rem;
              height: 0.75rem;
              padding: 0;
            }
          }
        }
      }
    }
  }
}
</style>
