<template>
    <div class="course-search-block"
         @click="tabSearch">
        <a href="javascript:void(0)"
           class="course-btn">{{getLabelVal}}</a>
        <span class="el-input__suffix" style="position: inherit">
            <span class="el-input__suffix-inner"><i
                class="el-select__caret el-input__icon"
                :class="{'el-icon-arrow-down':!showSelectBox,'el-icon-arrow-up':showSelectBox}"></i>
            </span>
        </span>
        <div class="hide-course-list"
             v-if="showSelectBox">
            <div class="item"
                 @click="selectAll"
                 v-if="courseList.length>0"
                 :class="{'current':val.length===courseList.length}"
                 :key="'all'">
                <a href="javascrpt:void(0)">全部</a>
            </div>
            <div class="item"
                 :class="{'current':val.indexOf(item.course_id)>-1}"
                 @click="selectItem($event,item)"
                 :key="item.course_id"
                 v-for="item in courseList">
                <a href="javascrpt:void(0)">{{item.course_name}}</a>
            </div>
            <div class="border-up"><span></span></div>
        </div>
    </div>
</template>

<script>
    import {mapActions} from 'vuex'

    let root = null
    let thisPageRoute = null
    export default {
        props: {
            value: {
                type: Array
            },
            defaultAllSelect:{
                type: Boolean,
                default: false
            }
        },
        mounted() {
            thisPageRoute = this.$route
            this.getCourseList()
            if (!this.$route.meta.keepAlive) {
                this.bindRootClick()
            }
        },
        destroyed() {
            if (thisPageRoute && !thisPageRoute.meta.keepAlive) {
                this.unBindRootClick()
            }
        },
        data() {
            return {
                val: [],
                courseList: [],
                checkedAll: false,
                showSelectBox: false
            };
        },
        watch: {
            value(nv) {
                this.val = JSON.parse(JSON.stringify(nv))
            }
        },
        computed: {
            getLabelVal(){
                let result = ''
                result = this.val.length <= 0 ? '选择课程' : this.val.length===this.courseList.length?'全部':('已选择' + this.val.length + '门' + '课程');
                return result
            }
        },
        deactivated() {
            this.unBindRootClick()
        },
        activated() {
            this.bindRootClick()
        },
        methods: {
            ...mapActions('course', ['getCourseListActions']),
            tabSearch(e) {
                e.stopPropagation();
                this.showSelectBox = !this.showSelectBox
            },
            rootClickEvent(e) {
                e.stopPropagation()
                this.showSelectBox = false
            },
            bindRootClick() {
                root = document.documentElement || document.body
                root.addEventListener('click', this.rootClickEvent)
            },
            unBindRootClick() {
                if (root) {
                    root.removeEventListener('click', this.rootClickEvent)
                }
            },
            selectAll(e) {
                // e.stopPropagation()
                if (this.val.length === this.courseList.length) {
                    this.val = []
                } else {
                    this.val = this.courseList.map((item) => {
                        return item.course_id
                    })
                }
                this.$emit('input', this.val)
            },
            selectItem(e, item) {
                e.stopPropagation()
                let index = this.val.indexOf(item.course_id)
                if (index > -1) {
                    this.val.splice(index, 1)
                } else {
                    this.val.push(item.course_id)
                }
                this.$emit('input', this.val)
            },
            getCourseList() {
                this.getCourseListActions().then((res) => {
                    let {error_code, data} = res;
                    if (!error_code) {
                        data.forEach((item) => {
                            item.checked = false
                        })
                        this.courseList = data;
                        if(this.val.length==0){
                            if(this.defaultAllSelect){
                                this.selectAll();
                            }
                        }
                    }
                })
            }
        }
    };
</script>
<style lang="postcss" scoped>

</style>