<template>
    <div class="main-center">
        <div class="book-box">
            <ul class="book-list" v-if="bookList.length">
                <li class="common-bor"
                    @click="goBookTest(item)"
                    :key="index"
                    v-for="(item,index) in bookList">
                    <div class="img-box">
                        <img :src="item.bookImage" alt="">
                    </div>
                    <div class="tit" :title="item.bookName">{{item.bookName}}</div>
                    <div class="star-box">
                        <span class="star"></span>
                        <span class="count">{{item.userStar}}/{{item.totalStar}}</span>
                    </div>
                </li>
            </ul>
            <div class="flex-box-center" v-if="bookList.length">
                <el-pagination
                        background
                        :page-size.sync="page_size"
                        :current-page.sync="page_num"
                        layout="prev, pager, next"
                        @current-change="getBookListWeb"
                        :total="total">
                </el-pagination>
            </div>
        </div>
        <div class="has-not-child-box" v-if="!bookList.length">
            <img src="@/assets/img/empty-img.png" alt="">
            <p class="login-tit">你还没有绘本哦，快去报班获取绘本吧~</p>
        </div>
    </div>
</template>

<script>
    import {mapActions, mapGetters} from 'vuex'
    export default {
        data: () => ({
            page_num:1,
            page_size:6,
            bookList:[],
            total:0
        }),
        methods: {
            ...mapActions('course', ['getBookListWebActions']),
            getBookListWeb(){
                let params = {
                    page_num:this.page_num,
                    page_size:this.page_size
                }
                this.getBookListWebActions(params).then((res)=>{
                    let {error_code,data} = res;
                    if(!error_code){
                        this.bookList = data.rows
                        this.total = data.total
                    }
                })
            },
            goBookTest(item){
                this.goView('bookTest',{
                    webUrl:item.webPath,
                    from:'courseBookList'
                })
            }
        },
        watch: {},
        mounted() {
            this.getBookListWeb()
        },
        computed: {},
        components: {},
        created() {
        }
    }
</script>

<style lang="postcss" scoped>

</style>
